<template>
  <div id="pokemon">
    <div class="card">
      <div class="card-image">
        <figure>
          <img :src="currentImg" alt="Placeholder image"/>
        </figure>
      </div>
      <div class="card-content">
        <div class="media">
          <div class="media-content">
            <p class="title is-4">{{ num }}: {{ name | maj }}</p>
            <p class="subtitle is-6">{{pokemonInfo.type}}</p>
          </div>
        </div>

        <div class="content">
            <button class="button is-link is-outlined" @click="mudarSprite()">Frente/Verso</button>
        </div>
      </div>
    </div>


  </div>
</template>


<script>
import axios from "axios";
export default {
  created: function () {
    console.log("Pegou a informaÃ§ao de cada pokemon!");
    axios.get(this.url).then((res) => {
      this.pokemonInfo.type = res.data.types[0].type.name;
      this.pokemonInfo.back = res.data.sprites.back_default;
      this.pokemonInfo.front = res.data.sprites.front_default;
      this.currentImg=this.pokemonInfo.front;
      console.log(this.pokemonInfo);
    });
  },
  data() {
    return {
        isFront:true,
        currentImg:'',
      pokemonInfo: {
          type: '',
          front: '',
          back: ''
      },
    };
  },
  props: {
    num: Number,
    name: String,
    url: String,
  },
  filters: {
    maj: function (value) {
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
  },
  methods:{
      mudarSprite: function(){
          if(this.isFront){
              this.isFront=false;
              this.currentImg=this.pokemonInfo.back;
          } else{
              this.isFront=true;
              this.currentImg=this.pokemonInfo.front;
          }
      }
  }
};
</script>

<style scoped>
#pokemon  {
padding-top: 2%;
}
</style>