<template>
  <div id="app">
    <div class="column is-half is-offset-one-quarter">
      <h1>Abdul Kevin Alexis</h1>
      <h4 class="is-size-4">Pokedex</h4>
            <div class="control is-loading">
              <input type="text" class="input is-focused is-rounded" name="" id="" placeholder="Buscar Pokemon pelo nome" v-model="busca">
             <button class="button is-link is-outlined is-rounded is-fullwidth" id="btnBuscar" @click="buscar" >Buscar</button>
            </div>
           
            
     
       <div v-for="(pokemon,index) in filteredPokemons" :key="pokemon.url">
          <pokemon :name="pokemon.name" :url="pokemon.url" :num="index+1"/>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Pokemon from './components/Pokemon.vue';
export default {
  components: { Pokemon },
  name: 'App',
  data(){
    return{
      pokemons:[],
      filteredPokemons:[],
      busca:''
    }
  },
  created: function(){
   axios.get("https://pokeapi.co/api/v2/pokemon?limit=151&offset=0").then(res=>{
     console.log("Pegou a lista de Pokemon!")
     this.pokemons=res.data.results;
     this.filteredPokemons=res.data.results;
   })
  }, 
  methods:{
    buscar: function(){
          this.filteredPokemons=this.pokemons;
          if (this.busca=='' || this.busca== ' ') {
            this.filteredPokemons=this.pokemons;
          } else {
            this.filteredPokemons= this.pokemons.filter(pokemon=> pokemon.name== this.busca);
          }
    }
  },
  computed:{
    // resultadoBusca: function(){
    //   if (this.busca=='' || this.busca== ' ') {
    //     return this.pokemons;
    //   } else {
    //     return this.pokemons.filter(pokemon=> pokemon.name== this.busca);
    //   }
    // }
  }

 
}
</script>

